	module.exports = {"data":{"post":{"html":"<p>Today I think I'm thinking straight for the first time since Tuesday. Our satellite receiver has been replaced, so we're receiving clear pictures again. My cousin Tracy was my only family member affected by events... she was stranded in Alabama and couldn't fly home, so she rented a car for a 24-hour drive back to Minnesota. My brother called out of the blue yesterday evening, 'just to talk'. </p>\n<p>Alwin tells the story of <a href=\"http://www.vfth.com/2001/09/14\">caring for Mrs. L.</a></p>","fields":{"title":"Cloudy Skies","date":"15 Sep 2001","url":"https://papascott.de/2001-09-15-cloudy-skies/"}}},"pathContext":{"slug":"/2001-09-15-cloudy-skies/","prev":{"fields":{"title":"We're In This Together","date":"13 Sep 2001","slug":"/2001-09-13-were-in-this-together/"}},"next":{"fields":{"title":"Sunday at Home","date":"16 Sep 2001","slug":"/2001-09-16-sunday-at-home/"}}}}

